import React, { useEffect, useState } from "react";
import {
  BarChart,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  Bar,
} from "recharts";
import axios from "axios";
import "./App.css";
import backgroundImg from "./WBalkans.png";
const App = () => {
  const [data, setData] = useState(null);
  const [width, setWidth] = useState(400);
  const [adata, setAdata] = useState([]);
  const getData = async () => {
    try {
      const response = await axios.get("https://corona-api.com/countries");
      setData(response);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    setWidth(window.screen.width);
  }, [width]);
  useEffect(() => {
    getData();
  }, []);

  useEffect(() => {
    if (data != null) {
      let fdata = data.data.data;
      const forChart = fdata.map((item) => {
        if (
          item.name === "Albania" ||
          item.name === "Bosnia and Herzegovina" ||
          item.name === "Serbia" ||
          item.name === "Montenegro" ||
          item.name === "North Macedonia"
        )
          return {
            name: item.name,
            todayDeaths: item.today.deaths,
            todayConfirmed: item.today.confirmed,
            totalDeaths: item.latest_data.deaths,
            totalConfirmed: item.latest_data.confirmed,
            totalRecovered: item.latest_data.recovered,
            deathRate: parseFloat(
              item.latest_data.calculated.death_rate.toFixed(3)
            ),
            perMillion:
              item.latest_data.calculated.cases_per_million_population,
          };
        else return null;
      });
      const newfilter = forChart.filter((item) => item);
      setAdata(newfilter);
      // setUdata(thedata);
    }
  }, [data]);

  return (
    <React.Fragment>
      <div
        className="background"
        style={{ backgroundImage: `url('${backgroundImg}')` }}
      ></div>

      {/*  APPP ORIGINAl*/}
      <div className="App">
        <h1 className="title">Western Balkans COVID-19 data</h1>
        <div className="graph">
          <div className="graphbg" />
          <h1>Today's data*</h1>
          <BarChart width={800} height={300} data={adata}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis type="number" domain={[0, "dataMax"]} />
            <Tooltip />
            <Legend />
            <Bar
              dataKey="todayConfirmed"
              fill="#1b78e3"
              name={"Confirmed cases"}
            />
            <Bar dataKey="todayDeaths" fill="#e31b1b" name={"Deaths "} />
          </BarChart>
        </div>
        <div className="graph">
          <h1 style={{ zIndex: "100" }}>Total cases and recovered</h1>
          <div className="graphbg" />
          <BarChart width={800} height={300} data={adata}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            {/* ticks={["Albanija","BiH","Hrvatska","Srbija","Crna Gora","S.Makedonija"]} */}
            <YAxis type="number" domain={[0, "dataMax"]} />
            <Tooltip />
            <Legend />
            <Bar
              dataKey="totalConfirmed"
              name={"Confirmed cases"}
              fill="#1b78e3"
            />
            <Bar
              dataKey="totalRecovered"
              name={"Number of recovered"}
              fill="#82ca9d"
            />
          </BarChart>
        </div>
        <div className="graph">
          <div className="graphbg" />
          <h1>Number of cases per 1.000.000* </h1>
          <BarChart width={800} height={300} data={adata}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis type="number" domain={[0, 5]} />
            <Tooltip />
            <Legend />
            <Bar dataKey="perMillion" fill="#1b78e3" name={"Cases per 1M"} />
          </BarChart>
        </div>
        <div className="graph">
          <div className="graphbg" />
          <h1>Total Deaths</h1>
          <BarChart width={800} height={300} data={adata}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis type="number" domain={[0, "dataMax"]} />
            <Tooltip />
            <Legend />
            <Bar dataKey="totalDeaths" fill="#e31b1b" name={"Total deaths"} />
          </BarChart>
        </div>

        <div className="graph">
          <div className="graphbg" />
          <h1>Death rate in %</h1>
          <BarChart width={800} height={300} data={adata}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis type="number" domain={[0, 5]} />
            <Tooltip />
            <Legend />
            <Bar dataKey="deathRate" fill="#e31b1b" name={"Death rate in %"} />
          </BarChart>
        </div>
      </div>
      
      {/*  APPP SSSSSSSS///////////////////////////////////////////////*/}
      <div className="App-S">
        <h1 className="title-s">Western Balkans COVID-19 data</h1>
        <div className="graph-s">
          <div className="graphbg-s">
            <h1 className="subtitle-s">Today's data*</h1>
            <BarChart width={300} height={220} data={adata}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis type="number" domain={[0, "dataMax"]} />
              <Tooltip />
              <Legend />
              <Bar
                dataKey="todayConfirmed"
                fill="#1b78e3"
                name={"Confirmed cases"}
              />
              <Bar dataKey="todayDeaths" fill="#e31b1b" name={"Deaths "} />
            </BarChart>
          </div>
        </div>
        <div className="graph-s">
          <div className="graphbg-s">
            {" "}
            <h1 className="subtitle-s" style={{ zIndex: "100" }}>
              Total cases and recovered
            </h1>
            <BarChart width={300} height={220} data={adata}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              {/* ticks={["Albanija","BiH","Hrvatska","Srbija","Crna Gora","S.Makedonija"]} */}
              <YAxis type="number" domain={[0, "dataMax"]} />
              <Tooltip />
              <Legend />
              <Bar
                dataKey="totalConfirmed"
                name={"Confirmed cases"}
                fill="#1b78e3"
              />
              <Bar
                dataKey="totalRecovered"
                name={"Number of recovered"}
                fill="#82ca9d"
              />
            </BarChart>
          </div>
        </div>
        <div className="graph-s">
          <div className="graphbg-s">
            <h1 className="subtitle-s">Number of cases per 1.000.000* </h1>
            <BarChart width={300} height={200} data={adata}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis type="number" domain={[0, 5]} />
              <Tooltip />
              <Legend />
              <Bar dataKey="perMillion" fill="#1b78e3" name={"Cases per 1M"} />
            </BarChart>
          </div>
        </div>
        <div className="graph-s">
          <div className="graphbg-s">
            <h1 className="subtitle-s">Total Deaths</h1>
            <BarChart width={300} height={220} data={adata}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis type="number" domain={[0, "dataMax"]} />
              <Tooltip />
              <Legend />
              <Bar dataKey="totalDeaths" fill="#e31b1b" name={"Total deaths"} />
            </BarChart>
          </div>
        </div>
        <div className="graph-s">
          <div className="graphbg-s">
            <h1 className="subtitle-s">Death rate in %</h1>
            <BarChart width={300} height={220} data={adata}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis type="number" domain={[0, 5]} />
              <Tooltip />
              <Legend />
              <Bar
                dataKey="deathRate"
                fill="#e31b1b"
                name={"Death rate in %"}
              />
            </BarChart>
          </div>
        </div>
      </div>
      <p className="msg">
        All data is provided by: https://corona-api.com/countries{" "}
      </p>
      <p className="msg">
        0 means lack of data or outdated data, data updates at 3 p.m.
        CET +1 *
      </p>
    </React.Fragment>
  );
};

export default App;

